import React from 'react';
import axios from 'axios';
import styled from 'styled-components';
import '../styles/main.css'

class Home extends React.Component<any, any> {
  constructor(props: any) {
    super(props);
    this.state = {
      data: null,
      loading: false,
    }
  }

  componentDidMount() {
    this.Loaditem();
  }

  Loaditem = async () => {
    const response = await axios.get('http://localhost:8000/api/rooms');
    this.setState({ data: response.data, loading: true });
  }


  render() {
    if (this.state.loading) {
    return (
      <div>
        {this.state.data.map((item: any) => (
          <table>
          <thead>
            <tr>
              <th colSpan={2}>클래스룸 정보</th>
              <th colSpan={2}>접속자 정보</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>수업명</td>
              <td>{item.name}</td>
              <td colSpan={2}>현재 접속자 수:</td>
            </tr>
            <tr>
              <td colSpan={2} rowSpan={2}>수업정보</td>
              <td rowSpan={2}>교수자</td>
              <td rowSpan={2}></td>
            </tr>
            <tr>
            </tr>
            <tr>
              <td>시작시간</td>
              <td>종료시간</td>
              <td rowSpan={2}>학습자</td>
              <td rowSpan={2}></td>
            </tr>
            <tr>
              <td></td>
              <td></td>
            </tr>
          </tbody>
          </table>
        ))}
      </div>
    )
  } else {
    return (
      <div>
        <p>Loading...</p>
      </div>
    )}
  }
}

export default Home;