import axios from 'axios';
import React from 'react';
import { logindata } from '../types/logindata.type';
import styles from 'styled-components';
import { setCookie, getCookie } from '../cookie';

const Container = styles.div`

`;

class Register extends React.Component<any, any> {

  handleSubmit = async (e: any) => {
    e.preventDefault();
    const response = await this.loginhandler({
      id: e.target.id.value,
      password: e.target.password.value,
    });
    
    setCookie('Bearer', response.data);
  }

  loginhandler = async (formData: logindata) => {
    console.log(formData)
    return await axios.post('http://localhost:8000/api/webusers/', formData)
  }

  render() {
    if (getCookie('Bearer') === undefined) {
      return (
        <Container>
          <form onSubmit={this.handleSubmit}>
            <input type="id" name="id" />아이디
            <input type="password" name="password" />비밀번호
            <input type="submit" value="회원가입" />
          </form>
        </Container>
      );
    } else {
      return (
        <Container>
          <div>로그인됨</div>
        </Container>
      )
    }
  }
}

export default Register;