/* eslint-disable no-restricted-globals */
import axios from 'axios';
import React from 'react';
import { logindata } from '../types/logindata.type';
import styled from 'styled-components';
import { setCookie, getCookie } from '../cookie';

const Container = styled.div`
  display: flex;
`;

const Form = styled.form`

`;

const Input = styled.input`
  background-color: orange;
`;

class Login extends React.Component<any, any> {

  handleSubmit = async (e: any) => {
    e.preventDefault();
    await this.loginhandler({
      id: e.target.username.value,
      password: e.target.password.value
    });
    location.reload();
  }

  loginhandler = async (formData: logindata) => {
    try {
      const response = await axios.post('http://localhost:8000/api/auth/login/', formData);
      setCookie('Bearer', response.data);
    } catch (error) {
      alert('로그인 실패');
    }
  }

  render() {
    if (getCookie('Bearer') === undefined) {
      return (
        <Container>
          <Form onSubmit={this.handleSubmit}>
            <Input type="text" name="username" placeholder="ID"/>
            <Input type="password" name="password" placeholder="password"/>
            <Input type="submit" name="로그인" value="로그인"></Input>
          </Form>
        </Container>
      );
    } else {
      return (
        <Container>
          <div>로그인됨</div>
        </Container>
      );
    }
  }
}

export default Login;